export default {
	data() {
		return {
			num: 0,
			titleList: [{
				name: '全部'
			}, {
				name: '待付款'
			}, {
				name: '待配送'
			}, {
				name: '待收货'
			}, {
				name: '待评价'
			}]
		}
	},
	onLoad(e) {
		if (e._num == undefined) {
			this.num = 0
		} else {
			this.num = e._num
		}
	},
	methods: {
		toConfirmOrder() {
			uni.navigateTo({
				url:'../../confirmOrder/confirmOrder'
			})
		},
		menuClick(index) {
			this.num = index
		},
		toOrderDetail() {
			uni.navigateTo({
				url:'orderDetail/orderDetail'
			})
		}
	}
}
